::selection {
  background: #bbb !important;
}

body {
  overflow-x: hidden;
}

a {
  color: #ccc;
  text-decoration: none;
}

a:hover {
  color: #333;
}

.byline {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
  font-weight: 900;
  color: #888;
}

.byline a {
  color: #888;
}

/*COLOR LEGENDS*/
.legend label, .legend span {
  display: block;
  float: left;
  height: 15px;
  width: 35px;
  text-align: center;
  font-size: 12px;
  color: #808080;
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
}

/*SEARCH BOX FILTERS*/
#filter {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
  font-size: .9em;
  width: 100% !important;
}

#filter input {
  width: 100% !important;
  padding: 0 10px 0 10px;
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
  border-radius: 5px !important;
  margin: 0;
  width: 100%;
  height: 30px;
  line-height: 120%;
  font-size: 1em;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  border-radius: 0;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.75);
  -webkit-transition: all 0.30s ease-in-out;
  -moz-transition: all 0.30s ease-in-out;
  -ms-transition: all 0.30s ease-in-out;
  -o-transition: all 0.30s ease-in-out;
  background-color: #fff !important;
  font-family: inherit;
  border: 1px solid #eee !important;
  background-size: 14px 14px;
  background-repeat: no-repeat;
  background-position: 98% 50%;
}

#filter input:focus {
  width: 100% !important;
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
  box-shadow: 0 0 5px #999;
  padding: 1px 10px 1px 10px;
  border: 1px solid #61bd1a;
}

/*VARIOUS TITLES AND CHATTER*/
.chartTitle {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
  font-weight: 900;
  margin-top: 15px;
  font-size: 1.2em;
}

.chatter {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
  font-size: .9em;
  margin-top: 5px;
}

p {
  font-family: "Poynter Serif RE", Georgia, Times, serif;
  font-size: 15px;
  line-height: 140%;
  margin-bottom: 14px;
  letter-spacing: -0.3px;
}

.demographic {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
  font-size: .9em;
}

.source {
  font-size: .5em;
  color: #ccc;
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
}

/*HIGHLIGHTED NUMBERS*/
.num {
  font-family: "Popular", Helvetica, Arial, sans-serif;
  font-weight: 900;
}

.bigNum {
  font-family: "Popular", Helvetica, Arial, sans-serif;
  font-weight: 900;
  font-size: 3.5em;
}

/*BOOTSTRAP BUTTON CHECKBOX STYLES*/
.btn-group {
  margin-left: 0;
  width: 100%;
  text-align: center;
}

.btn-primary {
  width: 19.6%;
  text-align: center;
  background-color: #333;
  border: 0;
  border-radius: 0;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: arial;
  font-size: 13px;
  font-weight: 900;
  moz-border-radius: 0;
  padding: 13px 26px;
  text-decoration: none;
  webkit-border-radius: 0;
}

.btn-primary input[type=checkbox] {
  border: none;
  display: none !important;
}

.btn-primary:hover {
  background-color: #5cbf2a;
}

.active {
  background-color: #5cbf2a;
}

/*D3 SLIDER STYLES*/
#sliderDiv {
  height: 20px;
  padding: 15px;
}

.d3-slider-handle {
  position: absolute;
  width: 1.2em;
  height: 1.2em;
  border: 1px solid #ddd !important;
  border-radius: 0;
  background: #ddd !important;
  z-index: 3;
}

.d3-slider-handle:hover {
  background: #333 !important;
  cursor: pointer;
}

/*D3 MAP STYLES*/
.background {
  fill: none;
  pointer-events: all;
}

.states {
  fill: #aaa;
}

#state-borders {
  fill: none;
  stroke: #fff;
  stroke-width: 2px;
  stroke-linejoin: round;
  stroke-linecap: round;
  pointer-events: none;
}

.tooltip {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
  background-color: white;
  height: auto;
  width: auto;
  padding: 10px;
  border-radius: 0;
  border: 1px solid black;
  font-size: 13px;
  font-family: Arial;
}

.states .active {
  fill: #333 !important;
  fill-opacity: 1 !important;
}

.faded {
  fill-opacity: 0.5 !important;
}

.states path:hover {
  fill: #333 !important;
  cursor: pointer;
}

/*C3 TOOLTIPS*/
th, text {
  color: #000 !important;
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
  font-size: 11px;
}

.tips th {
  background-color: #fff !important;
}

.tips {
  border: none !important;
}

.value {
  color: #000 !important;
  font-family: "Popular", Helvetica, Arial, sans-serif;
  font-weight: 900;
}

.name {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
}

/*NVD3 STYLES*/
.nvd3 .nv-x .nv-axis .tick line, .nvd3 .nv-y .nv-axis .tick line {
  fill: none;
  display: none !important;
  stroke: #fff;
  shape-rendering: crispEdges;
  opacity: 0 !important;
}

.nvtooltip {
  border-radius: 0 !important;
  background-color: #fff;
  border-color: #000;
}

text {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
}

.nv-axisMaxMin {
  font-family: "Popular", Helvetica, Arial, sans-serif !important;
}

/*NAV BUTTONS*/
.myButtonNav {
  background-color: #ddd;
  border: 0;
  border-radius: 0;
  color: #000;
  cursor: pointer;
  display: block;
  font-family: "Benton Sans",Helvetica,Arial,sans-serif;
  font-size: 1em;
  font-weight: 900;
  moz-border-radius: 0;
  padding: 6px;
  text-decoration: none;
  webkit-border-radius: 0;
  width: 100%;
  margin: 5px;
}

.myButtonNav:hover {
  background-color: #378f00 !important;
}

.myButtonNav:active {
  background-color: #5cbf2a;
}

/*STANDARD TOGGLE BUTTONS*/
